<template>
  <v-navigation-drawer v-model="$store.state.drawer" absolute temporary>
    <v-list>
      <v-list-item>
        <v-list-item-avatar>
          <img v-if="photoURL" :src="photoURL" />
        </v-list-item-avatar>
        <v-list-item-content>
          <v-list-item-title>{{ userName }}</v-list-item-title>
        </v-list-item-content>
      </v-list-item>

      <v-divider></v-divider>

      <!-- routerを拡張したものなので、":to="でリンク先のnameを指定すればページ遷移する -->
      <v-list-item v-for="(item, index) in items" :key="index" :to="item.link">
        <v-list-item-icon>
          <v-icon>{{ item.icon }}</v-icon>
        </v-list-item-icon>
        <v-list-item-content>
          <v-list-item-title>{{ item.title }}</v-list-item-title>
        </v-list-item-content>
      </v-list-item>
    </v-list>
  </v-navigation-drawer>
</template>

<script>
import { mapGetters } from "vuex";

export default {
  data() {
    return {
      items: [
        { title: "連絡先一覧", icon: "mdi-menu", link: { name: "Addresses" } },
      ],
    };
  },
  // このコンポーネントの1つのプロパティとして、getterの戻り値(ここではuserの状態)を参照できる
  // computed: getterとsetterが使える。
  // computed: 一度行った計算結果はキャッシュで保持し、変化がなければそのキャッシュ値を返す。
  // ⇒ gettersではcomputed(返り値がpropatyのため)を、actionsではmethods(返り値が関数のため)で定義する方式である。
  computed: {
    ...mapGetters(["userName", "photoURL"]),
  },
};
</script>
